.App {
  height: 100vh;
  width: 100vw;
}

.App_header {
  height: 60px;
  width: 100vw;
  position: relative;
  min-height: 25px;
  /*background-color: #f7d5cc;*/
  background-color: rgba(247,213,204,0.5);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  color: #f7f8eb;
}

.App_header_div {
  height: 100%;
  width: 100px;
  line-height: 60px;
  text-align: center;
  background-color: #7e658d;
}

.App_header_div_off {
  height: 100%;
  width: 100px;
  line-height: 60px;
  text-align: center;
  background-color: #d9a5ef;
}

.App_header_sections {
  height: 40px;
  width: 100px;
  line-height: 40px;
  text-align: center;
  background-color: #7e658d;
  border-radius: 5px;
}

.App_header_sections_off {
  height: 40px;
  width: 100px;
  line-height: 40px;
  text-align: center;
  background-color: #d9a5ef;
  border-radius: 5px;
}
